.modal{display:none;top:0;left:0;height:100%;width:100%;position:fixed;z-index:1000;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center}.modal__close{position:absolute;right:25px;top:18px;cursor:pointer;padding:10px}.modal__close div{background:#999;width:1px;height:16px;position:absolute;top:0}.modal__close div:first-child{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.modal__close div:nth-child(2){-webkit-transform:rotate(135deg);transform:rotate(135deg)}.modal__backdrop{z-index:1000;top:0;left:0;height:100%;width:100%;background-color:rgba(0,0,0,.4);-webkit-transition:opacity .4s cubic-bezier(.25,.8,.25,1);-o-transition:opacity .4s cubic-bezier(.25,.8,.25,1);transition:opacity .4s cubic-bezier(.25,.8,.25,1);opacity:0}.modal__container{-webkit-box-shadow:0 2px 6px rgba(0,0,0,.07),0 9px 20px rgba(0,0,0,.07);box-shadow:0 2px 6px rgba(0,0,0,.07),0 9px 20px rgba(0,0,0,.07);border-radius:7px;background:#fff;z-index:1001;position:absolute;-webkit-transform:scale(.7);transform:scale(.7);opacity:0;-webkit-transition:.15s cubic-bezier(0,0,.2,1);-o-transition:.15s cubic-bezier(0,0,.2,1);transition:.15s cubic-bezier(0,0,.2,1)}.modal_open{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex}.modal_animate .modal__backdrop,.modal_animate .modal__container{opacity:1;-webkit-transform:none;transform:none}